.theme-switch__icon {
  color: currentColor;
  height: 24px;
  width: 24px;
  margin-left: 8px;
  margin-right: 8px;
}

.theme-switch__control {
  width: 64px;
  height: 32px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #fd5103;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 26px;
  height: 26px;
  z-index: 2;
  pointer-events: none;
  transition: transform 250ms var(--timing-function);
  background-color: #fd5103;
  border-radius: 50%;
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #fd5103;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  background-color: #fcfcfc;
  transform: translateX(35px);
}

.theme-switch__toggle:checked + .theme-switch__track {
  border: 1px solid #fff;
}

.theme-switch__toggle:focus + .theme-switch__track, .theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 0 0 3px #fcfcfc;
}

